.nav__list {
	display: flex;
	flex-wrap: wrap;
	margin: 0;
	padding: 0;
	list-style-type: none;
}

@media (max-width: @max-md) {

	.header__nav-right,
	.nav {
		display: none;
	}
}

@media (min-width: @min-md) {
	.nav__list {
		justify-content: center;
		width: fit-content;
		margin: 0 auto;
	}
}
